@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Playfair Display', serif;
   font-family: 'Poppins', sans-serif;   */

h1 {
  font-family: "Playfair Display", serif;
}

/* ======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: #22272b;
  border: 1px solid #22272b;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: #22272b;
  color: white;
}

/* ======== registration page css ======= */
.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}

.reg-heading {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  color: #22272b;
}

.reg-form {
  width: 300px;
  overflow: hidden;
}

.link-warning,
.link-success {
  text-decoration: none;
}

/* ======== side-menu css ======= */

.admin-menu {
  margin-top: 63px;
}

.side-nav-container {
  background-color: #061b28;
  width: 300px;
  height: 90vh;
  position: relative;
  color: #ffffff;
  transition: 0.4s;
}

@media screen and (max-width: 500px) {
  .side-nav-container {
    background-color: #061b28;
    width: 300px;
    height: 55vh;
    position: relative;
    color: #ffffff;
    transition: 0.4s;
  }
}

.side-nav-container-NX {
  width: 85px;
}

.dash-text {
  font-size: 31.5px;
}

.nav-upper,
.nav-heading,
.nav-menu,
.menu-item {
  display: grid;
}

.nav-heading {
  grid-template-columns: 2fr, 1fr;
  grid-template-rows: 1fr;
  height: 75px;
}

.nav-brand {
  display: flex;
  font-family: "Poppins", sans-serif;
  margin-left: 8px;
  margin-top: 8px;
}
.hamburger {
  position: absolute;
  right: 0;
  background: none;
  border: none;
  cursor: pointer;
  margin: auto -35px;
  background-color: #55717e;
  border: 2px solid #ffffff;
  padding: 20px;
  border-radius: 90px;
  height: 71px;
  width: 74px;
}

.hamburger span {
  display: block;
  margin-top: 5px;
  background-color: #ffffff;
  border-radius: 15px;
  height: 4px;
  width: 30px;
  transition: 0.4s;
}

.hamburger-in:hover span:nth-child(1) {
  width: 25px;
  transform: translate(-3px, 9.5px) rotate(30deg);
}

.hamburger-in:hover span:nth-child(2) {
  display: none;
}
.hamburger-in:hover span:nth-child(3) {
  width: 25px;
  transform: translate(-3px, -9.5px) rotate(-30deg);
}

.hamburger-out:hover span:nth-child(1) {
  width: 25px;
  transform: translate(0px, 9.5px) rotate(-30deg);
}

.hamburger-out:hover span:nth-child(2) {
  display: none;
}
.hamburger-out:hover span:nth-child(3) {
  width: 25px;
  transform: translate(0px, -9.5px) rotate(30deg);
}

.nav-menu {
  grid-template-rows: repeat(7, 1fr);
  margin-top: 40px;
}

.menu-item {
  height: 57px;
  width: 16rem;
  display: flex;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  margin: auto 20px;
  border-radius: 10px;
  position: relative;
}

.menu-icon {
  font-size: 40px;
  margin-top: 7px;
  margin-left: 5px;
}

.menu-item-NX {
  height: 57px;
  width: 3.5rem;
  display: flex;
  color: #ffffff;
  text-decoration: none;
  margin: auto 20px;
  border-radius: 10px;
  position: relative;
}

.menu-item:hover {
  background-color: #22272b;
}

.menu-item-NX:hover {
  background-color: #22272b;
}

/* ======== category-dashboard css ======= */
.table-primary {
  --bs-table-bg: #55717e;
}

/* ======== product-dashboard css ======= */
.prod-card-main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 60px;
}

.prod-card-main-home {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  grid-column-gap: 150px;
}

.prod-card-main .card {
  padding: 10px 0 0;
}

.prod-card-main-home .card {
  padding: 10px 0 0;
}

.card .prod-image {
  margin: auto;
  position: relative;
}

.card .prod-image:hover {
  transition: 0.5s;
  transform: scale(1.1);
}

.card .prod-image img {
  width: 200px;
  height: 200px;
  transition: 0.3s ease-in-out;
}

.card .card-body {
  width: 80%;
  margin: auto;
  margin-top: 20px;
}

.card .card-body h5 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
}

.card .card-body p {
  font-size: 14px;
  margin: 10px 0;
}

.card span {
  font-size: 20px;
  text-decoration: none;
}

@media screen and (max-width: 1300px) {
  .prod-card-main {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 3rem;
  }

  .card .prod-image {
    width: 90%;
  }
}

@media screen and (max-width: 1300px) {
  .prod-card-main-home {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 3rem;
  }
}

@media screen and (max-width: 1000px) {
  .prod-card-main {
    grid-template-columns: repeat(1, 1fr);
    padding: 5rem 3rem;
  }
}

@media screen and (max-width: 950px) {
  .prod-card-main-home {
    grid-template-columns: repeat(1, 1fr);
    padding: 5rem 3rem;
  }
}

@media screen and (max-width: 500px) {
  .prod-card-main {
    grid-template-columns: repeat(1, 1fr);
    padding: 5rem 4rem;
    align-items: center;
  }
}

@media screen and (max-width: 500px) {
  .prod-card-main-home {
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
  }

  .btn-warning {
    margin-top: -100px;
  }

  .btn-outline-secondary {
    margin-top: -100px;
  }
}

/* ======== product-homepage css ======= */

.prod-btn {
  display: flex;
}

.card-body .btn-primary {
  background-color: #9315d6;
  border-color: #9315d6;
  width: 75px;
}

.card-body .btn-primary:hover {
  background-color: #b575d7;
  border-color: #b575d7;
}

.card-body .btn-dark {
  width: 75px;
}

.prod-price {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
}

.prod-desc {
  font-family: "Poppins", sans-serif;
}

.prod-heading {
  margin-top: 30px;
  margin-bottom: 25px;
  font-family: "Poppins", sans-serif;
}

.btn-warning {
  margin-left: 200px;
}

@media screen and (max-width: 500px) {
  .btn-warning {
    margin-left: -20px;
  }
}

.btn-outline-success {
  border-color: #9315d6;
  color: #9315d6;
  --bs-btn-active-bg: #660a97;
  --bs-btn-active-border-color: #660a97;
  margin-top: 4px;
}

.btn-outline-success:hover {
  background-color: #9315d6;
  border-color: #9315d6;
}

.btn-outline-dark {
  background-color: #9315d6;
  border-color: #9315d6;
  color: #ffffff;
}

.btn-outline-dark:hover {
  background-color: #660a97;
  border-color: #660a97;
  --bs-btn-active-bg: #660a97;
  --bs-btn-active-border-color: #660a97;
  color: #ffffff;
}

.size-selector {
  width: 5rem;
  height: 1.6rem;
}

/* ======== contact-homepage css ======= */

.contact_form .form-control {
  padding: 1.375rem 0.75rem;
  line-height: 1.5;

  border-radius: 0 !important;
  border: 2px solid #9315d6;
}

.contact_form input.form-control {
  margin-bottom: 2rem;
  height: calc(2.5rem + 0.75rem + 2px);
}

.btn-primary {
  background-color: #9315d6;
  font-size: 17px !important;
  font-weight: 600 !important;
  color: #ffffff !important;
  width: 70px;
  border-color: #9315d6;
}

.btn-primary:hover {
  background-color: #be6ceb;
  font-size: 17px !important;
  font-weight: 600 !important;
  border-color: #be6ceb;
  --bs-btn-active-bg: #be6ceb;
  --bs-btn-active-border-color: #be6ceb;
}

/* ======== upate-profile user css ======= */

.update-profile {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.payment-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 500px) {
  th:nth-child(3),
  td:nth-child(3) {
    display: none;
  }

  th:nth-child(1),
  td:nth-child(1) {
    display: none;
  }

  th:nth-child(4),
  td:nth-child(4) {
    display: none;
  }

  th:nth-child(6),
  td:nth-child(6) {
    display: none;
  }
}
