.navbar-brand {
  font-family: "Playfair Display", serif;
  font-size: 25px;
  font-weight: 700;
  color: #fff !important;
  letter-spacing: 1px;
}

.navbar-brand span {
  color: #9315d6;
}

.nav-link {
  font-weight: 500 !important;
  font-size: 17px !important;
  color: #fff !important;
}

.nav-link:hover {
  color: #9315d6 !important;
}

.navbar-toggler {
  background-color: #fff;
}

.nav-link.btn.btn-primary {
  background-color: #9315d6;
  font-size: 17px !important;
  font-weight: 600 !important;
  color: #22272b !important;
  width: 70px;
}

.nav-link.btn.btn-primary:hover {
  background-color: #cb8aee;
}

.nav-link.btn.btn-success {
  background-color: #fff;
  font-size: 17px !important;
  font-weight: 600 !important;
  color: #22272b !important;
  width: 70px;
}

.nav-link.btn.btn-success:hover {
  background-color: #ffffff85;
}

.user-icon {
  font-size: 24px;
  margin-bottom: 5px;
}

.dropdown-item {
  font-size: 15px !important;
  font-weight: 400 !important;
  color: #22272b !important;
}

.custom-dropdown-item {
  background-color: #fff !important;
}

.custom-dropdown-item:hover {
  background-color: #cb8aee !important;
}

@media screen and (max-width: 500px) {
  .nav-link.btn.btn-success {
    margin-left: 15.7px;
  }
}
