.Footer {
  color: #fff;
  padding: 2rem 0 0;
}

.ft-col1 h3 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  letter-spacing: 2px;
  padding: 0.5rem 0;
}

.ft-col1 p {
  font-family: "Poppins", sans-serif;
  padding: 0rem 4rem 0.5rem 0;
  font-weight: 500;
}

.ft-col1 span {
  color: #9315d6;
}

.footer-icons {
  margin-top: 20px;
}

.footer-icons svg {
  width: 25px;
  height: 25px;
  margin: 0 0.5rem 0.5rem;
  fill: #9315d6;
  cursor: pointer;
}

.footer-icons svg:hover {
  fill: #fff;
  cursor: pointer;
  transition: 0.6s;
}

.Footer h5 {
  font-family: "Playfair Display", serif;
  color: #9315d6;
}

.ft-col2 ul {
  list-style: none;
  padding-left: 0;
}

.ft-col2 ul li {
  padding: 0.35rem 0;
  font-weight: 500;
}

.ft-col2 ul a {
  text-decoration: none;
  color: #fff;
  font-family: "Poppins", sans-serif;
}

.ft-col3 p {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  padding: 0.35rem 0;
}

.ft-col3 p svg {
  width: 25px;
  height: 25px;
  padding-right: 0.5rem;
}

.Last-Footer {
  background: #9315d6;
  text-align: center;
  padding: 1.5rem 0;
  margin-top: 15px;
}

.Last-Footer p {
  font-family: "Playfair Display", serif;
  letter-spacing: 1px;
  font-weight: 500;
  font-size: 1.2rem;
}

.Last-Footer p span {
  font-family: "Poppins", sans-serif;
}
